      // line-height: 4.8rem;
      // letter-spacing: initial;
    }
    &:first-child {
      // width: 24.5%;
      // border-right: 0.2rem solid @dark-brown;
    }
    &:last-child {
      // text-align: right;
      // width: 30%;
      // border-left: 0.2rem solid @dark-brown;
    }
  }

  .about__title {
    position: static;
    left: 0;
    width: auto;
    margin-bottom: 24px;

    font-size: 60px;
    line-height: 60px;
    text-align: left;
  }

  .about__links-list {
    flex-direction: row-reverse;
