.banner-green {
  color: @dark-brown;
  background-color: @green;
}

.banner-green__content-wrap {
  padding-top: 8.5rem;
  padding-bottom: 8.5rem;

  text-align: center;
}

.banner-green__sub-title {
  margin-bottom: 1.3rem;
  font-size: 2.4rem;
}

.banner-green__title,
.banner-green__main-title {
  font-family: @cinema;
  text-transform: uppercase;
}

.banner-green__title {
  margin-bottom: 1.5rem;
  font-size: 8.5rem;
}

.banner-green__desc {
  line-height: 2.5rem;
  text-align: center;
  &-item {
    display: block;
  }
  &--mw {
    max-width: 56rem;
    margin-left: auto;
    margin-right: auto;
  }
}

.banner-green__main-title {
  margin-bottom: 1.3rem;
  font-size: 30rem;
  line-height: 30rem;
  &--fz350 {
    margin-top: 2.2rem;
    margin-bottom: 0;
    font-size: 35rem;
    line-height: 35rem;
  }
  &--desc-hidden {
    display: none;
  }
}

@media (max-width: @tablet-width) {
  .banner-green__content-wrap {
    padding-top: 7rem;
    padding-bottom: 4rem;
  }

  .banner-green__sub-title {
    margin-bottom: 2rem;
    font-size: 2rem;
  }

  .banner-green__title {
    font-size: 6rem;
    line-height: 6rem;
    &-item {
      display: block;
    }
  }

  .banner-green__desc {
    // line-height: 2.5rem;
    &--break {
      max-width: 32rem;
      margin-left: auto;
      margin-right: auto;
    }
    &--mw {
      max-width: none;
    }
    &-item {
      // display: block;
    }
  }

  // .banner-green__main-title {
  //   margin-bottom: 1.3rem;
  //   font-size: 30rem;
  //   line-height: 30rem;
  //   &--fz350 {
  //     margin-top: 2.2rem;
  //     margin-bottom: 0;
  //     font-size: 35rem;
  //     line-height: 35rem;
  //   }
  // }

  .banner-green__main-title {
    margin-top: 1.3rem;
    margin-bottom: 1.8rem;
    font-size: 11rem;
    line-height: 10rem;
    &--fz350 {
      margin-top: 4.3rem;
      margin-bottom: 4.3rem;
      font-size: 20rem;
      line-height: 17rem;
    }
    &--desc-hidden {
      display: inline-block;
    }
    &--mob-hidden {
      display: none;
    }
  }
}

